<template>
    <div class="container">
        <div class="header">
            <p class="title pe">请填写信息</p>
        </div>

        <div class="info-group">
            <van-cell-group :border="false">
                <van-field class="field" center clearable input-align="center" v-model="realName"
                           placeholder="请输入您的姓名"/>
                <van-field class="field" center clearable input-align="center" v-model="carType" placeholder="请输入车型号"/>
                <van-field class="field" center clearable input-align="center" v-model="company"
                           placeholder="请输入租赁公司"/>
            </van-cell-group>
        </div>

        <van-button @click="onSubmit" class="confirm">认证</van-button>

    </div>
</template>

<script>
    import {CellGroup, Field, Button} from 'vant';
    import {PERFECT} from '../../common/config'
    import {post} from '../../common/http'
    import store from '../../storage'

    export default {
        components: {
            [CellGroup.name]: CellGroup,
            [Field.name]: Field,
            [Button.name]: Button
        },

        data() {
            return {
                data: {},
                realName: '',
                carType: '',
                company: '',
                from: ''
            };
        },

        computed: {},

        methods: {
            onSubmit() {

                const params = {
                    phone: this.data.phone,
                    realName: this.realName,
                    carType: this.carType,
                    company: this.company
                }
                post(PERFECT, params, () => {
                    this.data = {...this.data, ...params}
                    store.save(this.data)
                    this.$router.push({name: 'info', query: {from: this.from}})
                })
            },
        },
        created() {
            this.data = store.fetch()
            this.from = this.$route.query.from
        }
    };
</script>

<style lang="less">

    @import "../../assets/common.less";

    @height: 0.90rem;

    .info-group {
        margin-top: @height;
        padding-left: 0.95rem;
        padding-right: 0.69rem;
    }


</style>
